//main layouts

//main site container
#site-wrapper {
	position: relative;
	background-color: ac(white);
	height: 100%;
}

main {
	padding: 4rem 0;
}

.start-page {
	padding: 0;
	background-image: url(../img/assets/bg-site.svg);
	background-size: cover;
	background-color: ac(third);
	height: calc( 100% - 13rem);
	display: flex;
	flex-flow: row nowrap;
	align-content: center;
	justify-content: center;
}

.woocommerce ul.products[class*="columns-"] li.product, .woocommerce-page ul.products[class*="columns-"] li.product {
	margin-right: 2%;
}

#main {
	height: 100%;
	background-color: ac(white);
	max-width: 1080px;
	margin: 0 auto;
}

#site-header {
	padding: 4rem 0;
	background-color: ac(primary);
	position: relative;
	&::after {
		content: '';
		position: absolute;
		bottom: -2rem;
		display: block;
		width: 100%;
		height: 2.6rem;
		background-image: url('../img/assets/bot_primary.png');
	}
}

#site-footer {
	padding: 4rem 0;
	background-color: ac(primary);
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: -2rem;
		display: block;
		width: 100%;
		height: 2.6rem;
		background-image: url('../img/assets/top_primary.png');
	}
}

.loc-section {
	margin-top: 8rem;
	@media (min-width: $b-medium) {
		margin-top: 0;
	}
}

#rest-header {
	padding: 4rem 0;
	background-color: ac(primary);
	position: relative;
	&::after {
		content: '';
		position: absolute;
		bottom: -2rem;
		display: block;
		width: 100%;
		height: 2.6rem;
		background-image: url('../img/assets/bot_primary.png');
	}
}

#rest-header hgroup {
	display: grid;
	grid-template-columns: 1fr;
	grid-auto-rows: auto;
	justify-items: center;
	align-items: center;
	text-align: center;
	& .header-logo {
		// text-align: center;
		margin-top: 1.5rem;
		& img {
			height: 100%;
			min-height: 15rem;
		}	
	}
	& .address {
		margin-top: 1rem;
		& h3 {
			font-size: 2rem;
		}
	}
	@media (min-width: $b-medium) {
		grid-template-columns: repeat(3, 1fr);
	}
}

#child-footer {
	text-align: center;
	background-color: ac(primary);
	padding: 4rem;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: -2rem;
		left: 0;
		display: block;
		width: 100%;
		height: 2.6rem;
		background-image: url('../img/assets/top_primary.png');
	}
	& h3 {
		margin-top: 2rem;
	}
}

.other_page {
	background-color: ac(third);
	& article {
		max-width: 75rem;
		margin: 0 auto;
		padding: 6rem 2rem;
		text-align: center;
	}
}

.woocommerce form .form-row-first, .woocommerce form .form-row-last, .woocommerce-page form .form-row-first, .woocommerce-page form .form-row-last {
	width: 100%;
}